/* variables */
:root {
  --bg-size: cover;
  --shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
  --margin-desktop: 0 0 10px 50px;
  --fit: fit-content;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway', sans-serif !important;
  color: white !important;
}

.main {
  flex-basis: 100%;
  display: flex;
  flex-wrap: wrap;

  background-size: var(--bg-size);
  background-position: center;
  background-color: #000;

  width: 100vw;
  height: 100vh;
}

.current-weather {
  flex-basis: 25%;
  display: flex;
  flex-direction: column;

  padding-left: 10px;
}

#weather-icon {
  width: 120px;
  height: 120px;

  margin: 60px 0 0 40px;

  background-size: var(--bg-size);
}

#weather-status {
  font-weight: 600;
  font-size: 2.5rem;

  margin: 10px 0 10px 50px;
}

#location {
  font-weight: 300;

  margin: var(--margin-desktop);
}

#date {
  font-weight: 600;

  margin: var(--margin-desktop);
}

#time {
  margin-left: 100px;

  font-weight: 200;
}

.current-degrees {
  font-weight: 800;
  font-size: 4.5rem;
  margin: var(--margin-desktop);
}

#changeLoc {
  font-size: 0.8rem;
}

#change-location-btn {
  font-weight: 100;
  text-align: start;
  color: #fff;

  margin: 20px 0 10px 50px;
  padding: 0 5px;
  width: var(--fit);
  position: relative;

  background: none;
  border: none;
}

i.fas.fa-map-marker-alt {
  margin-right: 10px;
}

#collapseExample {
  width: 250px;
  align-self: auto;
  position: absolute;
}

.settings {
  flex-basis: 50%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.dropdown-item {
  cursor: pointer;
}

.menu {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 5px 0;
  margin-top: 4vh;
  width: var(--fit);

  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: var(--shadow);
}

#refresh-bg-btn {
  display: flex;
  justify-content: center;
  align-items: center;

  background-image: url('./assets/image.svg');
  background-size: var(--bg-size);

  border: 1px solid #fff;

  width: 40px;
  height: 40px;

  margin: 0 10px;
}

.dropdown-menu {
  min-width: var(--fit) !important;
}

.dropdown {
  margin: 0 10px;
}

#units {
  width: 80px;
  height: 40px;
  margin: 0 10px;
}

#units > .btn {
  padding: 0;
}

#language-btn {
  width: 55px;
  height: 40px;
  padding: 0;
}

#show-location-btn {
  display: none;
  justify-content: center;
  align-items: center;

  background-image: url('./assets/map.svg');
  background-size: var(--bg-size);

  border: 1px solid #fff;

  width: 40px;
  height: 40px;

  margin: 0 10px;
}

.menu > button, .menu > div {
  box-shadow: var(--shadow);
}

.detailed-forecast {
  flex-basis: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  padding-top: 80px;
  height: var(--fit);
}

.detailed-forecast > div > span {
  font-size: 1.25rem;
  font-weight: 100;
}

.detailed-forecast > div > p {
  font-size: 2em;
  font-weight: 700;

  margin-bottom: 50px;
}

.detailed-forecast > div > i {
  margin-right: 10px;
}

.detailed-forecast:first-child{
  margin-top: 85px;
}

.future-forecast {
  flex-basis: 75%;
  display: flex;
  justify-content: flex-end;
}

.cards-wrapper {
  flex-basis: 75%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.cards-wrapper > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-basis: 25%;

  margin: 0 20px;
  height: 20vh;

  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: var(--shadow);
}

.cards-wrapper > div > .day {
  margin: 15px 0;

  font-weight: 200;
}

.forecast {
  display: flex;
  justify-content: center;

  width: 100%;
}

.forecast > .icon {
  width: 80px;
  height: 80px;
  background-size: var(--bg-size);
}

.forecast > .degrees {
  align-self: flex-end;
  font-size: 2.5rem;
  font-weight: 600;
}

.forecast > .units {
  align-self: flex-end;
  font-size: 2.5rem;
  font-weight: 600;
}

.location {
  flex-basis: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.coordinates {
  display: flex;
  align-items: center;
}

.coordinates > p {
  margin: 0;
  margin: 5px;
}

#map {
  width: 75%;
  height: 25vh;

  border-radius: 10px;
  box-shadow: var(--shadow);;
}

.latitude-icon {
  width: 1rem;
  height: 1rem;

  background-image: url('./assets/latitude.svg');
}

.longitude-icon {
  width: 1rem;
  height: 1rem;

  background-image: url('./assets/longitude.svg');
}

.active-unit {
  background-color: #545b62 !important;
}

.show {
  display: flex !important;
}

/* media queries (height) */

@media screen and (min-height: 1024px) {
  .current-weather {
    flex-basis: 30%;
    padding-left: 0;
  }
  .settings {
    flex-basis: auto;
  }
  .detailed-forecast {
    flex-basis: 25% !important;
  }
  .cards-wrapper > div {
    height: 15vh;
  }
  .cards-wrapper > div > .day {
    font-size: 1.5rem;
  }
  .forecast > .units {
    font-size: 1.5rem;
  }
  .forecast > .degrees {
    font-size: 1.5rem;
  }
  .future-forecast {
    flex-basis: 65%;
  }
  #map {
    height: 15vh;
  }
}

@media screen and (max-height: 425px) {
  .main {
    align-items: center;
    justify-content: space-between;
  }
  .location {
    display: none;
  }
  .settings {
    order: 10;
    flex-basis: 100% !important;
    justify-content: flex-end;
    margin-right: 20px;
  }
  .menu {
    height: var(--fit);
    margin-top: -4vh;
  }
  .current-weather {
    flex-basis: 30%;
  }
  #weather-icon {
    width: 10vh;
    height: 10vh;
    margin: 0 0 0 20px;
  }
  #weather-status {
    margin: 5px 0 10px 20px;
    font-size: 1.5rem;
  }
  #location {
    margin: 0 0 10px 20px;
    font-size: 1rem;
  }
  #date {
    margin: 0 0 10px 20px;
    font-size: 1rem;
  }
  #time {
    margin-left: 40px;
    font-size: 1.1rem;
  }
  .current-degrees {
    margin: 0 0 10px 37px;
    font-size: 2rem;
  }
  #change-location-btn {
    margin-left: 20px; 
    margin-bottom: 5px;
    font-size: 0.7rem;
    margin-top: 0;
  }
  #collapseExample {
    width: 30vw;
  }
  .detailed-forecast {
    flex-basis: 30%;
    padding-top: 0;
    order: 5;
  }
  .detailed-forecast > div > span {
    font-size: 0.8rem;
  }
  .detailed-forecast > div > p {
    margin-bottom: 10px;
    font-size: 1rem;
  }
  .future-forecast {
    flex-basis: 35%;
    justify-content: space-evenly;
    height: var(--fit);
  }
  .cards-wrapper {
    flex-basis: 100%;
    justify-content: space-evenly;
  }
  .cards-wrapper > div {
    height: var(--fit);
    justify-content: center;
    margin: 0;
  }
  .cards-wrapper > div > .day {
    font-size: 0.7rem;
  }
  .forecast > .icon {
    width: 1.5rem;
    height: 1.5rem;
  }
  .forecast > .degrees {
    font-size: 1rem;
  }
  .forecast > .units {
    font-size: 1rem;
  }
  .location {
    order: 15;
    flex-basis: 100%;
    align-items: flex-end;
  }
  #map {
    width: 40%;
    height: 18vh;
  }
  #show-location-btn {
    display: flex;
  }
}

/* media queries (width) */

@media screen and (min-width: 768px) {
  .current-weather {
    flex-basis: 30%;
    padding-left: 0;
  }
  .settings {
    flex-basis: 40%;
  }
  .detailed-forecast {
    flex-basis: 30%;
  }
  #weather-status {
    font-size: 2rem;
  }
}

@media screen and (max-width: 425px) {
  .location {
    display: none;
    order: 5;
    align-items: center;
    flex-basis: 100%;
  }
  .settings {
    order: 10;
    flex-basis: 100%;
  }
  .menu {
    height: var(--fit);
  }
  .current-weather {
    flex-basis: 50%;
    font-size: 80%;
  }
  #weather-icon {
    width: 10vh;
    height: 10vh;
    margin: 30px 0 0 20px;
  }
  #weather-status {
    margin: 5px 0 10px 20px;
    font-size: 1.5rem;
  }
  #location {
    margin: 0 0 10px 20px;
    font-size: 1rem;
  }
  #date {
    margin: 0 0 10px 20px;
    font-size: 1rem;
  }
  #time {
    margin-left: 40px;
    font-size: 1.1rem;
  }
  .current-degrees {
    margin: 0 0 10px 20px;
    font-size: 3rem;
  }
  #change-location-btn {
    margin-left: 20px; 
    margin-bottom: 5px;
    font-size: 0.7rem;
  }
  #collapseExample {
    width: 65vw;
  }
  .detailed-forecast {
    flex-basis: 50%;
    padding-top: 40px;
    font-size: 80%;
  }
  .detailed-forecast > div > span {
    font-size: 1rem;
  }
  .detailed-forecast > div > p {
    margin-bottom: 30px;
  }
  .future-forecast {
    flex-basis: 100%;
    justify-content: space-evenly;
    height: var(--fit);
  }
  .cards-wrapper {
    flex-basis: 100%;
    justify-content: space-evenly;
  }
  .cards-wrapper > div {
    height: 15vh;
    justify-content: center;
    margin: 10px 5px;
  }
  .cards-wrapper > div > .day {
    font-size: 1rem;
  }
  .forecast > .icon {
    width: 20px;
    height: 20px;
  }
  .forecast > .degrees {
    font-size: 1.5rem;
  }
  .forecast > .units {
    font-size: 1.5rem;
  }
  .location {
    position: absolute;
    width: fit-content;
    bottom: 12vh;
    right: 1%;
  }
  #map {
    width: 100%;
    height: 18vh;
  }
  #show-location-btn {
    display: flex;
  }
}
